table {
  border-collapse: collapse;
  width: 100%;
}

td, th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
}

tr:nth-child(even) {
  background-color: #dddddd;
}

body {
  margin: 40px;
}

html {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  line-height: 1.5;
}

div {
  font-size: 14px;
}

li {
  display: inline;
}

li a {
  display: block;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

li span {
  font-size: 16px;
  color: #f2f2f2;
  text-align: center;
  text-decoration: none;
}

li a:hover {
  background-color: #216E37;
  color: white;
}

ul {
  list-style-type: none;
  margin: -40px -40px 0;
  padding: 0;
  overflow: hidden;
  background-color: #219653;
}

.menu-form {
  display: inline;
}

.menu-button {
  font-size: 16px;
  border-radius: 0;
  line-height: 1.5;
  text-align: center;
  text-decoration: none;
  background-color: #219653;
  border: none;
  color: white;
  padding: 14px 16px;
  display: inline-block;
  cursor: pointer;
}

.menu-button:hover {
  background-color: #216E37;
  color: white;
}

.menu-text {
  margin-right: 14px;
}

.error-list {
  color: red;
  background-color: white;
}

.error-list-item {
  color: red;
}
